<template>
    <div>
        <h1>用户列表</h1>
        <table border="1">
            <tr>
                <td>编号</td>
                <td>姓名</td>
                <td>年龄</td>
                <td>生日</td>
                <td>操作</td>
            </tr>
            <tr v-for="user in users">
                <td>{{ user.id }}</td>
                <td>{{ user.name  }}</td>
                <td>{{ user.age  }}</td>
                <td>{{ user.birth }}</td>
                <td><a href="javascript:;" @click="deleteUser(user.id)">删除</a>&nbsp;
                    <a :href="'#/user/userModify?id='+user.id">修改</a> </td>
            </tr>
        </table>
        <a href="#/user/addUser">添加</a>
        <router-view></router-view>
    </div>
</template>

<script>
export default {
    name: "User",
    data(){
        return{
            users:[]
        }
    },
    methods:{
        findAll(){
            this.$http.get("http://localhost:8081/vue/findAll")
                .then(
                    (response) => {
                        console.log(response.data)
                        this.users = response.data;
                    });
        },

        deleteUser(id){
            this.$http.get("http://localhost:8081/vue/deleteUserById?id="+id)
            .then(
                (response) => {
                    console.log(response.data);
                    if(response.data.isSuccess){
                        alert(response.data.msg);
                        this.findAll();
                    }
                }
            );
        }
    },
    components:{},
    created() {
        this.findAll();
    },
    watch:{   //用来监听路由
        $route:{
            handler:function (val){
                if(val.path == "/user"){
                    this.findAll();
                }
            },
            //深底观察监听
            deep:true
        }
    }
}
</script>

<style scoped>

</style>



