<template>
    <div>
        <h2>用户修改</h2>
        <form>
            用户名：<input v-model="user.name" type="text"><br>
            年龄：<input v-model="user.age" type="text"><br>
            生日：<input v-model="user.birth" type="text"><br>
            <input type="button" @click="modifyUserInfo" value="修改">
        </form>
    </div>
</template>

<script>
export default {
    name: "UserModify",
    data(){
        return {
            user:{}
        }
    },
    methods:{
        findUserById(id){
            this.$http.get("http://localhost:8081/vue/findUserById?id="+id)
            .then(
                (response) => {
                    console.log(response.data);
                    this.user = response.data;
                }
            )
        },

        modifyUserInfo(){
            this.$http.post("http://localhost:8081/vue/update",this.user)
            .then(
                (response) => {
                    console.log(response);
                    if(response.data.isSuccess){
                        this.$router.push("/user");
                    }
                });
        }
    },
    created() {
        console.log(this.$route.query.id);   //this.$route.query.id，query可以拿到url里？的值，
                                             //如果是restful风格则是param
        this.user.id = this.$route.query.id;
        this.findUserById(this.user.id);
    }
}
</script>

<style scoped>

</style>



