<template xmlns="http://www.w3.org/1999/html">
    <div>
        <h2>添加用户信息</h2>
        <form>
            用户名：<input v-model="user.name" type="text"><br>
            年龄：<input v-model="user.age" type="text"><br>
            生日：<input v-model="user.birth" type="text"><br>
            <input type="button" @click="saveUserInfo" value="添加用户">
        </form>
    </div>
</template>

<script>
export default {
    name: "AddUser",
    data(){
        return{
            user:{}
        }
    },
    methods:{
        saveUserInfo(){
            console.log(this.user);
            this.$http.post("http://localhost:8081/vue/add",this.user)
                      .then(
                          (response) => {
                              console.log(response);
                              if(response.data.isSuccess){
                                  this.$router.push("/user");   //如果添加成功则切换路由即重新查询所有用户
                              }
                          });
        }
    }
}
</script>

<style scoped>

</style>



